.profile-dropdown-wrapper {
    position: relative;
}

.profile-avatar-btn {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: #4285F4;
    color: white;
    font-size: 14px;
    font-weight: 600;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.15s;
    text-transform: uppercase;
}

.profile-avatar-btn:hover {
    opacity: 0.85;
}

.profile-dropdown-menu {
    position: absolute;
    top: calc(100% + 8px);
    right: 0;
    background: white;
    border: 1px solid #E8EAED;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
    min-width: 220px;
    z-index: 100;
    overflow: hidden;
}

.profile-dropdown-header {
    padding: 16px;
    background: #F8F9FA;
    border-bottom: 1px solid #E8EAED;
}

.profile-dropdown-name {
    font-size: 14px;
    font-weight: 600;
    color: #202124;
    margin-bottom: 2px;
}

.profile-dropdown-email {
    font-size: 12px;
    color: #5F6368;
}

.profile-dropdown-role {
    display: inline-block;
    margin-top: 6px;
    font-size: 11px;
    font-weight: 500;
    background: #E8F0FE;
    color: #4285F4;
    padding: 2px 8px;
    border-radius: 999px;
}

.profile-dropdown-items {
    padding: 6px 0;
}

.profile-dropdown-item {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    padding: 10px 16px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 14px;
    color: #202124;
    text-align: left;
    transition: background 0.15s;
}

.profile-dropdown-item:hover {
    background: #F1F3F4;
}

.profile-dropdown-item.danger {
    color: #EA4335;
}

.profile-dropdown-item.danger:hover {
    background: #FEF2F2;
}

.profile-dropdown-divider {
    height: 1px;
    background: #E8EAED;
    margin: 4px 0;
}
