/* HomePage — auth-aware design */

.home-page {
    min-height: 100vh;
    background: #ffffff;
    display: flex;
    flex-direction: column;
}

/* ── Hero Section ─────────────────────────────────────────── */
.home-hero {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 80px 24px 60px;
    background: linear-gradient(135deg, #F8F9FA 0%, #EEF2FF 100%);
    text-align: center;
}

.home-hero-badge {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    background: #E8F0FE;
    color: #4285F4;
    font-size: 12px;
    font-weight: 600;
    padding: 6px 14px;
    border-radius: 999px;
    margin-bottom: 24px;
    letter-spacing: 0.3px;
}

.home-hero-title {
    font-size: 56px;
    font-weight: 800;
    color: #202124;
    letter-spacing: -0.03em;
    margin-bottom: 16px;
    line-height: 1.1;
}

.home-hero-title span {
    color: #4285F4;
}

.home-hero-subtitle {
    font-size: 20px;
    color: #5F6368;
    font-weight: 400;
    margin-bottom: 12px;
    max-width: 540px;
}

.home-hero-desc {
    font-size: 15px;
    color: #80868B;
    margin-bottom: 40px;
    max-width: 480px;
    line-height: 1.6;
}

.home-hero-actions {
    display: flex;
    gap: 12px;
    margin-bottom: 64px;
}

/* Buttons */
.home-btn-primary {
    padding: 12px 28px;
    background: #4285F4;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.15s;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    gap: 8px;
}

.home-btn-primary:hover { background: #3367D6; }

.home-btn-outline {
    padding: 12px 28px;
    background: white;
    color: #3C4043;
    border: 1.5px solid #DADCE0;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.15s;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
}

.home-btn-outline:hover { background: #F1F3F4; }

/* ── Feature Cards (logged out) ────────────────────────────── */
.home-features {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    max-width: 860px;
    width: 100%;
}

.home-feature-card {
    background: white;
    border: 1px solid #E8EAED;
    border-radius: 16px;
    padding: 28px 24px;
    text-align: left;
    box-shadow: 0 2px 8px rgba(0,0,0,0.04);
    transition: box-shadow 0.2s;
}

.home-feature-card:hover {
    box-shadow: 0 6px 20px rgba(66,133,244,0.1);
    border-color: #BEDBFF;
}

.home-feature-icon {
    font-size: 32px;
    margin-bottom: 14px;
    display: block;
}

.home-feature-title {
    font-size: 16px;
    font-weight: 600;
    color: #202124;
    margin-bottom: 8px;
}

.home-feature-desc {
    font-size: 13px;
    color: #5F6368;
    line-height: 1.5;
}

/* ── Action Cards (logged in) ─────────────────────────────── */
.home-welcome-title {
    font-size: 42px;
    font-weight: 700;
    color: #202124;
    margin-bottom: 8px;
}

.home-welcome-sub {
    font-size: 18px;
    color: #5F6368;
    margin-bottom: 48px;
}

.home-action-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    max-width: 860px;
    width: 100%;
}

.home-action-card {
    background: white;
    border: 1.5px solid #E8EAED;
    border-radius: 16px;
    padding: 32px 24px;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s;
    box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.home-action-card:hover {
    border-color: #4285F4;
    box-shadow: 0 8px 24px rgba(66,133,244,0.15);
    transform: translateY(-2px);
}

.home-action-icon {
    font-size: 40px;
    margin-bottom: 16px;
    display: block;
}

.home-action-title {
    font-size: 16px;
    font-weight: 600;
    color: #202124;
}

.home-action-desc {
    font-size: 13px;
    color: #5F6368;
    margin-top: 6px;
}

/* ── Loading ───────────────────────────────────────────────── */
.home-loading {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #5F6368;
    font-size: 16px;
}

/* Responsive */
@media (max-width: 768px) {
    .home-features,
    .home-action-cards {
        grid-template-columns: 1fr;
    }
    .home-hero-title { font-size: 36px; }
    .home-hero-actions { flex-direction: column; }
}